import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { getProjectRelativePath, Ajax } from "kit";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
const projectRelativePath = getProjectRelativePath();
// 初始化请求参数
Ajax.get(`${projectRelativePath}static/config/kmapue.config.json`).then(
  (res) => {
    console.log("res", res);
    window["ueConfig"] = res;
  }
);
Ajax.get(`${projectRelativePath}static/config/global.json`).then((config) => {
  const globalConfig = config;
  //1、全局配置注入window（深拷贝）
  window["globalConfig"] = globalConfig;

  root.render(<App />);
  // If you want to start measuring performance in your app, pass a function
  // to log results (for example: reportWebVitals(console.log))
  // or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
  reportWebVitals();
});
