import loadable from '@loadable/component'
const routes = [
  {
    path: '/',
    redirect: '/home',
    title: '首页',
    hidden: true
  },
  {
    path: '/home',
    title: '首页',
    component: loadable(() => import(/* webpackChunkName:'Home' */'./pages/home'))
  },
  {
    path: '/manage/:name',
    title: '管理类',
    component: loadable(() => import(/* webpackChunkName:'manage' */'./pages/manage'))
  },
  {
    path: '/optimize/:name',
    title: '优化类',
    component: loadable(() => import(/* webpackChunkName:'optimize' */'./pages/optimize'))
  },
  {
    path: '/about',
    title: '关于我们',
    redirect: '/about/news',
    children: [
      {
        path: 'news',
        title: '关于我们详情',
        component: loadable(() => import(/* webpackChunkName:'About' */'./pages/about/news'))
      },
    ],
  },
];
export default routes;